@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir-bold;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

:root {
  --input-border-size: 1px;
  --input-big-height: 70px;
  --input-small-height: 42px;
  --icon-big-size: 19px;
  --icon-small-size: 11px;
  --head-input-margin: 8px;
}

html, body {
  margin: 0;
  padding: 0;
}

.search {
  margin-top: 20px;
  margin-left: var(--head-input-margin);
  margin-right: var(--head-input-margin);
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

form[data-qa="big"] {
  height: var(--input-big-height);
  flex-shrink: 0;
}

form[data-qa="small"] {
  height: var(--input-small-height);
  flex-shrink: 0;
  padding-top: 20px;
}

.search__input {
  width: calc(100vw - 2 * var(--head-input-margin)  - 2 * var(--input-border-size));
  border: var(--input-border-size) solid #e1e7ed;
  border-radius: 4px;
  padding: 0;
  position: fixed;
  box-shadow: 0 1px 8px #3d4e611a;
}

.input--big {
  height: calc(var(--input-big-height)  - 2 * var(--input-border-size));
}

.input--small {
  height: calc(var(--input-small-height)  - 2 * var(--input-border-size));
}

.search__input::placeholder {
  text-align: left;
  color: #3d4e61;
  background-color: #fff;
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-origin: border-box;
  font-family: Avenir, sans-serif;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.input--big::placeholder {
  background-size: var(--icon-big-size) var(--icon-big-size);
  padding-left: calc(var(--icon-big-size)  + 17px + 26px);
  background-position: 25px;
  font-size: 16px;
}

.input--small::placeholder {
  background-size: var(--icon-small-size) var(--icon-small-size);
  padding-left: calc(var(--icon-small-size)  + 9px + 13px);
  background-position: 13px;
  font-size: 14px;
}

.input--big:hover {
  box-shadow: 0 4px 4px #00000040;
}

.input--small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.input--big:focus, .input--small:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
}

